<template>
  <v-container
    fill-height
    fluid
    grid-list-xl
  >
    <notifications
      class="mt-1"
      group="flush"
      position="top center"
    />

    <v-layout
      justify-center
      wrap
    >
      <v-flex
        md12
      >
        <material-card
          color="green"
          title="共有X篇文章"
          text="其中，草稿X篇，已发布X篇。当前第X页。"
        >
          <v-data-table
            :headers="headers"
            :items="items"
            hide-actions
          >
            <template
              slot="headerCell"
              slot-scope="{ header }"
            >
              <span
                class="subheading font-weight-light text-success text--darken-3"
                v-text="header.text"
              />
            </template>
            <template
              slot="items"
              slot-scope="{ item }"
            >
              <td>{{ item.alias ? item.alias : item._id }}</td>
              <td>
                <a :href="'/post/'+item._id"> {{ item.title }} </a>
              </td>
              <td>{{ item.add_date | fmtDate }}</td>
              <td>{{ item.update_date | fmtDate }}</td>
              <td class="text-xs-right">{{ item.status | fmtST }}</td>
            </template>
          </v-data-table>
        </material-card>
      </v-flex>
      <div class="text-xs-center">
        <v-pagination
          v-model="pagination.page"
          :length="pagination.total"
          :total-visible="pagination.visible"
          circle
          @input="pgInput"
        />
      </div>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    pagination: {
      total: 1,
      visible: 7,
      page: 1
    },
    headers: [
      {
        sortable: false,
        text: 'Alias',
        value: '_id'
      },
      {
        sortable: false,
        text: '标题',
        value: 'title'
      },
      {
        sortable: true,
        text: '创建时间',
        value: 'add_date'
      },
      {
        sortable: true,
        text: '更新时间',
        value: 'update_date'
      },
      {
        sortable: true,
        text: '状态',
        value: 'status',
        align: 'right'
      }
    ],
    items: [
      {
        _id: 'demo-id',
        title: 'demo-title',
        add_date: null,
        update_date: null,
        status: 1
      }
    ]
  }),

  mounted: function () {
    let vm = this
    vm.loadData()
  },

  methods: {
    pgInput: function () {
      let vm = this
      vm.loadData()
    },

    loadData: function () {
      let vm = this

      vm.$http.get(vm.$gc.H + '/pvt/query/post', {
        params: {
          page: vm.$data.pagination.page
        }
      })
        .then(function (response) {
          let { pT, pV, rT, rL } = {
            pT: vm.$data.pagination.total,
            pV: vm.$data.pagination.visible,
            rT: response.data.total,
            rL: response.data.limit
          }
          vm.$data.items = response.data.result
          vm.$data.pagination.total = Math.round(rT / rL)
          vm.$data.pagination.visible = pT < pV ? pT : pV
        })
        .then(() => {
          vm.$notify({
            group: 'flush',
            type: 'success',
            text: '内容加载完成!'
          })
        })
    }
  }
}
</script>

